import { getStorage, ref, uploadBytesResumable, getDownloadURL ,uploadBytes , listAll} from 'firebase/storage';
import { storage } from "../../firebase";


 // 이미지 리사이즈 함수 (10MB로 줄이기)
 const resizeImage = (file, maxSizeMB = 1) => {
	return new Promise((resolve, reject) => {
		const img = new Image();
		const reader = new FileReader();

		reader.onload = (e) => {
			img.src = e.target.result;
		};

		reader.onerror = reject;
		reader.readAsDataURL(file);

		img.onload = () => {
			const canvas = document.createElement('canvas');
			const ctx = canvas.getContext('2d');

			// 원본 이미지의 비율을 유지하며 크기 조정
			const maxWidth = 1024; // 최대 너비
			const maxHeight = 1024; // 최대 높이
			let width = img.width;
			let height = img.height;

			// 비율에 맞춰서 이미지 크기 변경
			if (width > height) {
				if (width > maxWidth) {
					height = Math.round((height * maxWidth) / width);
					width = maxWidth;
				}
			} else {
				if (height > maxHeight) {
					width = Math.round((width * maxHeight) / height);
					height = maxHeight;
				}
			}

			canvas.width = width;
			canvas.height = height;
			ctx.drawImage(img, 0, 0, width, height);

			// canvas에서 Blob으로 변환하여 파일로 다시 만들기
			canvas.toBlob(
				(blob) => {
					if (blob.size / 1024 / 1024 > maxSizeMB) {
						reject('파일 크기가 너무 커서 리사이즈할 수 없습니다.');
					} else {
						resolve(blob);
					}
				},
				'image/jpeg',
				0.8 // 80% 품질로 압축
			);
		};
	});
};

const generateFileName = (fileName , path) => {
	// 현재 날짜를 'YYYYMMDD' 형식으로 포맷
	const currentDate = new Date();
	const formattedDate = `${currentDate.getFullYear()}${(currentDate.getMonth() + 1).toString().padStart(2, '0')}${currentDate.getDate().toString().padStart(2, '0')}`;

	// 밀리초 단위로 현재 시간을 얻기
	const hours = currentDate.getHours().toString().padStart(2, '0');
	const minutes = currentDate.getMinutes().toString().padStart(2, '0');
	const seconds = currentDate.getSeconds().toString().padStart(2, '0');
	const milliseconds = currentDate.getMilliseconds().toString().padStart(3, '0'); // 밀리초
	const timeFormatted = `${hours}${minutes}${seconds}${milliseconds}`;

	// 파일명 생성: '년월일_밀리초_파일명'
	const newFileName = `${path}${formattedDate}_${timeFormatted}.${fileName}`;

	return newFileName;
};

  // 이미지를 Firebase Storage에 업로드
  const uploadImage = (name , file, index ,isPublic , setUploadProgress) => {
    let path = "images/";
    if(!isPublic)
      path += "private/";
    else
      path += "public/main/";

    const newFileName = generateFileName(name , path);

    const storageRef = ref(storage, `${newFileName}`);
    const uploadTask = uploadBytesResumable(storageRef, file);

    uploadTask.on(
      'state_changed',
      (snapshot) => {
        const progress =
          (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        // 진행률 업데이트
        setUploadProgress((prevProgress) => {
          const newProgress = [...prevProgress];
          newProgress[index] = progress; // 해당 이미지의 업로드 진행률 업데이트
          return newProgress;
        });
        console.log('Upload is ' + progress + '% done');
      },
      (error) => {
        console.error('Error uploading image: ', error);
      },
      () => {
        getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
          console.log('File available at: ', downloadURL);
        });
      }
    );
  };


	// 이미지 불러오는 함수
	const fetchImages = async (setOutimages , path ="public/") => {
		const imagesRef = ref(storage, `images/${path}`);
		const imagesArray = []; // 받아올 이미지를 담을 배열
		console.log("fetchImages");

		try {
			const result = await listAll(imagesRef);
			for (let i = 0; i < result.items.length; i++) {
				const imageRef = result.items[i];
				const url = await getDownloadURL(imageRef);
				// 이미지 배열에 추가
				imagesArray.push({
					id: i + 1, // id는 1부터 시작
					src: url,  // 이미지 URL
					alt: `Image ${i + 1}`, // alt 텍스트
				});
			}
			// 상태 업데이트
			setOutimages(imagesArray);

		} catch (error) {
			console.error("이미지 불러오기 실패: ", error);
		}
	};


export { uploadImage, resizeImage , fetchImages };
